import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [index, setIndex] = useState(1);
  const toRotate = [ "Web Developer", "Web Designer", "UI/UX Designer" ];
  const period = 2000;


  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="tagline">Bienvenidos </span>
                <h1>{` Al Fogon`} </h1>
                  <p>Ubicado en el corazn de la ciudad, "El Fogn" se erige como un templo de la parrilla, donde el arte culinario y el sabor autntico convergen en una experiencia gastronmica inolvidable.
Desde el momento en que cruzas la puerta, el aroma tentador de las brasas encendidas te envuelve, preparndote para un festn de sabores intensos. El ambiente rstico y acogedor de "El Fogn" crea el teln de fondo perfecto para una velada relajada y llena de placeres culinarios.La estrella de este establecimiento es, sin duda, su parrilla maestra. Los cortes de carne, seleccionados meticulosamente, son cocinados a la perfeccin, logrando ese equilibrio perfecto entre lo jugoso y lo ahumado. Desde suculentas costillas hasta tiernos filetes, cada bocado es una explosin de sabor que transporta a los comensales a un viaje sensorial.</p>
                  <button onClick={() => console.log('connect')}>Lets Connect <ArrowRightCircle size={25} /></button>
              </div>}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={6} xl={5}>
            
          </Col>
        </Row>
      </Container>
    </section>
  )
}
